html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "Academy engraved let";
  src: url(../fonts/academy-engraved-let.TTF) format("truetype");
  font-weight: normal;
  font-style: normal;
}

a {
  color: #000;
  text-decoration: none;
}

.btn-nav {
  background: transparent url(/img/slider-arrow.png) no-repeat;
  width: 15px;
  height: 21px;
}

.out {
  max-width: 940px;
  margin: 0 auto;
}

.top {
  background-color: #dddddd;
  padding: 7px 8px 6px 12px;
}

.slider-adaptive {
  height: 0;
  position: relative;
  padding-bottom: 61.70%;
}

.nav {
  display: flex;
  justify-content: space-between;
  font-family: "Myriad Pro";
  font-size: 11px;
  font-weight: 400;
  line-height: 14.4px;
  letter-spacing: .7px;
  text-transform: uppercase;
}

@media only screen and (max-width: 500px) {
  .nav {
    flex-wrap: wrap;
  }
}

.right-nav, .left-nav {
  display: flex;
}

@media only screen and (max-width: 500px) {
  .right-nav, .left-nav {
    width: 100%;
    justify-content: center;
  }
}

.right-nav__items, .left-nav__items {
  padding-left: 7px;
}

.right-nav__items:first-child, .left-nav__items:first-child {
  padding-left: 0;
}

.right-nav__items:last-child a:after, .left-nav__items:last-child a:after {
  content: '';
}

.right-nav__item, .left-nav__item {
  color: #000000;
  text-decoration: none;
}

.right-nav__item:after, .left-nav__item:after {
  content: "|";
  right: 0;
  padding-left: 8px;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 90px;
  margin-top: 16px;
  margin-left: 8px;
  margin-bottom: 5px;
}

@media only screen and (max-width: 950px) {
  .logo {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media only screen and (max-width: 700px) {
  .logo {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 450px) {
  .logo {
    flex-flow: column;
    height: auto;
  }
}

.logo__info {
  font-family: "Academy engraved let";
  font-size: 18px;
  text-transform: uppercase;
}

@media only screen and (max-width: 700px) {
  .logo__info {
    width: 27%;
    text-align: center;
    line-height: 1.5;
  }
}

@media only screen and (max-width: 450px) {
  .logo__info {
    width: 90%;
  }
}

.main-logo {
  width: 183px;
  padding: 18px 20px 0;
  margin-top: -10px;
  background-color: #fff;
  border-radius: 20px;
  position: absolute;
  top: 25px;
  right: 50%;
  margin-right: -91px;
  text-align: center;
}

@media only screen and (max-width: 700px) {
  .main-logo {
    top: 30px;
    margin-top: 0;
    padding-top: 8px;
  }
}

@media only screen and (max-width: 500px) {
  .main-logo {
    top: 50px;
  }
}

@media only screen and (max-width: 450px) {
  .main-logo {
    position: static;
    margin-right: 0;
  }
}

.main-logo_footer {
  top: 10px;
  padding-top: 0;
  padding-bottom: 25px;
  border-radius: 0 0 20px 20px;
}

@media only screen and (max-width: 770px) {
  .main-logo_footer {
    position: static;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 450px) {
  .main-logo_footer {
    margin-right: auto;
  }
}

.socials {
  margin-top: 3px;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 450px) {
  .socials {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

.socials__img {
  margin-left: 20px;
}

.socials__img:first-child {
  margin-left: -5px;
}

.socials_footer {
  flex-flow: column;
}

.title {
  display: flex;
  align-items: center;
  margin-bottom: 19px;
}

.title_short .title__right-line {
  margin-left: auto;
  width: 160px;
  flex: 0 1 auto;
}

.title_short .title__left-line {
  margin-right: auto;
  width: 160px;
  flex: 0 1 auto;
}

.title__img {
  margin-left: 28px;
  margin-right: 28px;
}

.title__name {
  font-family: "Academy engraved let";
  font-size: 31px;
  text-transform: uppercase;
  margin-left: 18px;
  margin-right: 18px;
  margin-top: 2px;
}

.title__right-line {
  flex: 1 1 auto;
  border-bottom: 1px solid #dddddd;
  height: 12px;
  margin-right: 15px;
  position: relative;
  margin-top: -12px;
}

.title__right-line:after {
  content: "";
  display: block;
  position: absolute;
  height: 23px;
  width: 12px;
  background: transparent url(/img/arrow.png) no-repeat;
  transform: rotate(180deg);
  right: 0;
  margin-right: -13px;
}

.title__left-line {
  flex: 1 1 auto;
  border-bottom: 1px solid #dddddd;
  height: 12px;
  margin-left: 15px;
  margin-top: -12px;
}

.title__left-line:after {
  content: "";
  display: block;
  height: 23px;
  width: 12px;
  background: transparent url(/img/arrow.png) no-repeat;
  margin-left: -13px;
}

.back {
  margin-top: 46px;
  color: #777777;
  font-family: "Myriad Pro";
  font-size: 14px;
  font-weight: 400;
  line-height: 16.8px;
  text-transform: uppercase;
  text-align: center;
}

.products-nav {
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 14px;
  font-weight: 400;
  line-height: 16.8px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  letter-spacing: 0;
  padding-left: 6px;
  margin-bottom: 39px;
}

.products-nav_down {
  margin-top: 40px;
  margin-bottom: 25px;
}

.products-nav__list {
  display: flex;
}

@media only screen and (max-width: 570px) {
  .products-nav__list {
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media only screen and (max-width: 540px) {
  .products-nav__list {
    margin-right: 40px;
    margin-left: 40px;
  }
}

@media only screen and (max-width: 357px) {
  .products-nav__list {
    margin-right: 10px;
    margin-left: 10px;
  }
}

.products-nav__item {
  position: relative;
  padding-right: 21px;
}

@media only screen and (max-width: 540px) {
  .products-nav__item {
    margin-top: 10px;
  }
}

.products-nav__item:after {
  content: "|";
  display: block;
  position: absolute;
  top: 0;
  left: -10px;
}

.products-nav__item:first-child:after {
  display: none;
}

.products-nav__item:last-child {
  padding-right: 0;
}

@media only screen and (max-width: 540px) {
  .products-nav__item:nth-child(5):after {
    display: none;
  }
}

@media only screen and (max-width: 440px) {
  .products-nav__item:nth-child(5):after {
    display: block;
  }
}

@media only screen and (max-width: 540px) {
  .products-nav__item:nth-child(4) {
    padding-right: 0;
  }
}

@media only screen and (max-width: 440px) {
  .products-nav__item:nth-child(4) {
    padding-right: 21px;
  }
}

@media only screen and (max-width: 440px) {
  .products-nav__item:nth-child(4):after {
    display: none;
  }
}

@media only screen and (max-width: 440px) {
  .products-nav__item:nth-child(3) {
    padding-right: 0;
  }
}

.slider-block {
  display: none;
  height: auto;
}

.slider-block_is-active {
  display: block;
}

.slider {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 3px solid #dddddd;
  padding-top: 30px;
  margin-top: 4px;
  margin-bottom: 41px;
}

.slider__preview {
  width: 100%;
  height: 100%;
}

.slider__preview img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  max-height: 90%;
}

@media only screen and (max-width: 700px) {
  .slider__preview img {
    max-height: 80%;
  }
}

@media only screen and (max-width: 420px) {
  .slider__preview img {
    max-height: 70%;
  }
}

.slider__controls {
  padding-top: 4px;
  padding-right: 10px;
  padding-left: 10px;
  margin-top: -36px;
  width: 100%;
  height: 36px;
  background-color: #dddddd;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.slider-close {
  background: transparent url(/img/close.png) no-repeat;
  width: 21px;
  height: 21px;
}

.navigation {
  display: flex;
}

.navigation__prev {
  transform: rotate(180deg);
}

.navigation__next {
  margin-left: 7px;
}

.navigation__slide-number {
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 16px;
  font-weight: 700;
  line-height: 19.2px;
  padding-left: 15px;
  width: 47px;
}

.footer {
  position: relative;
  background-color: #dddddd;
  padding: 19px 9px 19px 12px;
}

@media only screen and (max-width: 770px) {
  .footer {
    padding-top: 0;
  }
}

.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 31px;
}

@media only screen and (max-width: 770px) {
  .info {
    flex-flow: column;
  }
}

.info__text {
  margin-top: -23px;
  padding-left: 2px;
  letter-spacing: 0.63px;
  width: 37%;
  color: #777777;
  font-family: "Myriad Pro";
  font-size: 12px;
  font-weight: 400;
  line-height: 14.4px;
}

@media only screen and (max-width: 770px) {
